.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.container {
  width: 360px;
  padding-left: 20px;
  padding-right: 20px;
}

.signup-form {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 400px;
}

.signin-form {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 300px;
}

.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  width: 100%;
  padding-bottom: 60px;
  h1 {
    padding-left: 15px;
  }
}

.description-text {
  font-size: 14px;
  display: flex;
  justify-content: flex-start;
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 30px;
}

.icon-button {
  background: linear-gradient(
    90deg,
    rgba(185, 23, 217, 1) 11%,
    rgba(199, 32, 192, 1) 31%,
    rgba(212, 40, 168, 1) 100%
  );
  width: 150px;
  height: 50px;
  color: white;
}

.create-account-btn {
  background: linear-gradient(
    90deg,
    rgba(185, 23, 217, 1) 11%,
    rgba(199, 32, 192, 1) 31%,
    rgba(212, 40, 168, 1) 100%
  );
  color: white;
  height: 48px;
  padding: 0px 30px;
  width: 100%;
  font-family: "Poppins";
}

.footer {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  width: 100%;
}

.redirect-button {
  padding-left: 8px;
  &:hover {
    cursor: pointer;
  }
}
