.intro-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  min-height: 600px;
  align-items: center;
  width: 80%;
  min-width: 350px;
  max-width: 800px;
  padding-bottom: 40px;
}

.intro-step {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  min-height: 700px;
  padding-top: 30px;

  & > h3 {
    color: #1976d2;
    padding-bottom: 30px;
  }
}

.intro-finished {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  min-height: 200px;

  & > p {
    color: rgb(10, 168, 10);
    font-size: 20px;
    text-align: center;
  }
}
