.course-container {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 900px;
  padding-bottom: 50px;

  & > h1 {
    padding-bottom: 40px;
  }

  & > p {
    padding-top: 40px;
    padding-bottom: 50px;
    text-align: justify;
  }
}

.course-header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding-bottom: 40px;

  & > h1 {
    padding-left: 20px;
  }
}

.course-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-width: 400px;
  max-width: 1000px;
  padding-top: 40px;
}

.course-nav-buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding-top: 80px;
}

.MuiTextField-root {
  width: 350px;
}

.task-done-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 2px solid rgb(93, 202, 93);
  background-color: rgb(183, 240, 183);
  border-radius: 20px;
  padding: 15px 15px 15px 15px;
  height: 200px;
}

.movie-frame {
  min-height: 550px;
  width: 100%;
  margin-bottom: 40px;
  border-radius: 20px;
}
