.home-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 90%;
  height: 600px;
}

.home-title {
  font-size: 48px;
  text-align: center;
  padding-bottom: 40px;
  & > strong {
    color: #9c27b0;
  }
}

.home-accordion {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 600px;
  padding-top: 40px;
}

.home-button {
  width: 150px;
}
