.dashboard-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  min-height: 600px;
}

.welcome-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  height: 120px;
}

.progress-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  min-height: 450px;
  min-width: 350px;
  border-radius: 50px;
  background: #ffffff;
  box-shadow: 15px 15px 30px #d9d9d9, -15px -15px 30px #ffffff;
  margin-bottom: 60px;
}
